.caption__wrapper {
  bottom: unit(0.5);
  position: absolute;
  right: unit(0.5);
  width: 80%;
  z-index: 10;

  .banner.-sidebar-offset & {
    bottom: unit(2);
  }

  .hero.-country-page & {
    bottom: unit(1);
  }

  @include respond-to('medium-large') {
    width: 40rem;

    .banner.-sidebar-offset & {
      bottom: unit(1);
    }

    .hero.-country-page & {
      bottom: unit(0.5);
    }

    .country-background__image &,
    .special-content-block__sidebar__image &,
    .results-progress__sidebar & {
      width: 80%;
    }
  }
}

.caption {
  background-color: rgba($color--white, 0.9);
  border-radius: 2px 2px 0 2px;
  bottom: 37px;
  font-size: 1.2rem;
  font-weight: $font-weight--bold;
  line-height: 1.5;
  opacity: 0;
  padding: unit(1) unit(1) unit(1) unit(1.5);
  position: absolute;
  right: 15px;
  transform: translate3d(0, -10px, 0);
  visibility: hidden;
  width: 100%;

  &::after {
    @include pseudo;
    border-bottom: 7px solid transparent;
    border-right: 7px solid rgba($color--white, 0.9);
    border-top: 7px solid transparent;
    bottom: -9px;
    height: 0;
    right: -1px;
    transform: rotate(135deg);
    width: 0;
  }

  [data-is] & {
    transition: visibility 0s linear 0.3s, opacity 0.3s $ease--snappy,
      transform 0.3s $ease--snappy;
  }

  [data-is='open'] & {
    display: block;
    opacity: 1;
    transform: translate3d(0, 0, 0);
    transition: visibility 0s linear 0s, opacity 0.3s $ease--snappy,
      transform 0.3s $ease--snappy;
    visibility: visible;
  }
}

.caption__toggle {
  appearance: none;
  background: transparent url(#{$path}/images/icon-info.svg) no-repeat center;
  border: none;
  bottom: 0;
  height: 34px;
  overflow: hidden;
  position: absolute;
  right: 0;
  text-align: left;
  text-indent: 34px;
  white-space: nowrap;
  width: 34px;
}
