/*
 * media queries
 * small:         30em
 * medium:        48.75em
 * medium-large:  61.25em
 * large:         75em
 * x-large:       87.5em
 */
.caption__wrapper {
  bottom: 1rem;
  position: absolute;
  right: 1rem;
  width: 80%;
  z-index: 10; }
  .banner.-sidebar-offset .caption__wrapper {
    bottom: 4rem; }
  .hero.-country-page .caption__wrapper {
    bottom: 2rem; }
  @media (min-width: 61.25em) {
    .caption__wrapper {
      width: 40rem; }
      .banner.-sidebar-offset .caption__wrapper {
        bottom: 2rem; }
      .hero.-country-page .caption__wrapper {
        bottom: 1rem; }
      .country-background__image .caption__wrapper,
      .special-content-block__sidebar__image .caption__wrapper,
      .results-progress__sidebar .caption__wrapper {
        width: 80%; } }

.caption {
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 2px 2px 0 2px;
  bottom: 37px;
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 1.5;
  opacity: 0;
  padding: 2rem 2rem 2rem 3rem;
  position: absolute;
  right: 15px;
  transform: translate3d(0, -10px, 0);
  visibility: hidden;
  width: 100%; }
  .caption::after {
    content: "";
    display: block;
    position: absolute;
    border-bottom: 7px solid transparent;
    border-right: 7px solid rgba(255, 255, 255, 0.9);
    border-top: 7px solid transparent;
    bottom: -9px;
    height: 0;
    right: -1px;
    transform: rotate(135deg);
    width: 0; }
  [data-is] .caption {
    transition: visibility 0s linear 0.3s, opacity 0.3s cubic-bezier(0.75, 0, 0.125, 1), transform 0.3s cubic-bezier(0.75, 0, 0.125, 1); }
  [data-is='open'] .caption {
    display: block;
    opacity: 1;
    transform: translate3d(0, 0, 0);
    transition: visibility 0s linear 0s, opacity 0.3s cubic-bezier(0.75, 0, 0.125, 1), transform 0.3s cubic-bezier(0.75, 0, 0.125, 1);
    visibility: visible; }

.caption__toggle {
  appearance: none;
  background: transparent url(/wp-content/themes/feedthefuture/assets//images/icon-info.svg) no-repeat center;
  border: none;
  bottom: 0;
  height: 34px;
  overflow: hidden;
  position: absolute;
  right: 0;
  text-align: left;
  text-indent: 34px;
  white-space: nowrap;
  width: 34px; }
